<template><undefined>
<h1>Tag 标签</h1>
<p>进行标记和分类的小标签。</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<ul>
<li>用于标记事物的属性和维度。</li>
<li>进行分类。</li>
</ul>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;v-tag&gt;标签&lt;/v-tag&gt;\n  &lt;v-tag closable @close=\&quot;onClose\&quot;&gt;标签&lt;/v-tag&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    methods: {\n      onClose() {\n        console.log('close');\n      }\n    }\n  };\n&quot;,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><template>
  <v-tag>标签</v-tag>
  <v-tag closable="" @close="onClose">标签</v-tag>
</template>


</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>基本标签的用法，可以通过添加<code>closable</code>变为可关闭标签。可关闭标签具有<code>close</code>事件。</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-tag</span>&gt;</span>标签<span class="hljs-tag">&lt;/<span class="hljs-name">v-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">v-tag</span> <span class="hljs-attr">closable</span> <span class="hljs-attr">@close</span>=<span class="hljs-string">"onClose"</span>&gt;</span>标签<span class="hljs-tag">&lt;/<span class="hljs-name">v-tag</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      onClose() {
        <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'close'</span>);
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div>
